.feedback-form {
  max-width: 480px;
  flex-direction: column;
  display: flex;
}

.feedback-form label {
  flex-direction: column;
  margin-bottom: 16px;
  display: flex;
}

input, textarea {
  font: inherit;
  padding: 8px;
}

textarea {
  resize: none;
  background: #ffbf00;
  background: -webkit-gradient( left top, left bottom, color-stop(0%, #ffbf00), color-stop(100%, #fa9f17) );
}

.feedback-form {
  width: 100%;
  background: #bc3220;
  background: -webkit-gradient( left top, right top, color-stop(0%, #bc3220), color-stop(29%, #db4a37), color-stop(50%, #db4a37), color-stop(71%, #db4a37), color-stop(100%, #bc3220) );
  filter: progid:DXImageTransform. Microsoft. gradient( startColorstr= "#bc3220", endColorstr= "#bc3220", GradientType= 1 );
  background: linear-gradient(to right, #bc3220 0%, #db4a37 29%, #db4a37 50%, #db4a37 71%, #bc3220 100%);
  border: 1px solid #b12816;
  border-radius: 3px;
  margin: 0 auto;
  padding: 2em;
  position: relative;
  box-shadow: 0 7px 16px -3px rgba(0, 0, 0, .5);
}

.feedback-form :before {
  content: "";
  height: 2px;
  width: 100%;
  position: absolute;
  top: -2px;
  left: 0;
}

@media screen and (min-width: 600px) {
  .login-form {
    width: 50vw;
    max-width: 15em;
  }
}

.flex-row {
  margin-bottom: 1em;
}

.logo {
  color: #fff;
  width: 100%;
  margin-bottom: 50px;
}

.logo h1 {
  text-align: center;
  text-shadow: -1px -2px #a91400, 0 1px #ff7e6d;
  font-size: 45px;
  font-weight: 900;
}

.lf--input {
  width: 100%;
  box-sizing: border-box;
  color: #fff;
  text-shadow: 1px 1px 1px #232323;
  background: #bc3220;
  background: -webkit-gradient( left top, left bottom, color-stop(0%, #bc3220), color-stop(100%, #fa5846) );
  filter: progid:DXImageTransform. Microsoft. gradient( startColorstr= "#bc3220", endColorstr= "#fa5846", GradientType= 0 );
  background: linear-gradient(#bc3220 0%, #fa5846 100%);
  border: 0;
  border-radius: 25px;
  flex: 1;
  padding: 1em 2em;
  font-family: Open Sans, Arial, sans-serif;
  font-size: .8rem;
}

.lf--input:focus {
  outline: none;
  transition: transform .15s;
  transform: scale(1.02);
}

.lf--submit {
  width: 100%;
  background: #ffbf00;
  background: -webkit-gradient( left top, left bottom, color-stop(0%, #ffbf00), color-stop(100%, #fa9f17) );
  filter: progid:DXImageTransform. Microsoft. gradient( startColorstr= "#ffbf00", endColorstr= "#fa9f17", GradientType= 0 );
  color: #fff;
  cursor: pointer;
  text-shadow: -1px -1px #b86200, 0 1px #ecb76b;
  background: linear-gradient(#ffbf00 0%, #fa9f17 100%);
  border: 0;
  border-radius: 30px;
  margin-top: 50px;
  padding: 1em;
  font-size: 1.85em;
  font-weight: 500;
  display: block;
  box-shadow: 0 7px 16px -3px rgba(0, 0, 0, .5);
}

.lf--submit:hover {
  background: linear-gradient(#ffbf00 0%, #fa9f17 100%);
  outline: none;
  transition: transform .15s;
  transform: scale(1.02);
}

.lf--forgot {
  color: #fff;
  text-align: center;
  float: left;
  margin-top: 2em;
  font-size: .5em;
}

::-webkit-input-placeholder {
  color: #fff;
}

::-moz-placeholder {
  color: #fff;
}

:-ms-input-placeholder {
  color: #fff;
}

::-ms-input-placeholder {
  color: #fff;
}

::placeholder {
  color: #fff;
}

